import {
    USER_SIGNIN_SUCCESS, USER_SIGNIN_FAIL, USER_SIGNOUT, USER_LIST, DASHBAORD_DATA, USER_CHANGE_PASSWORD, USER_APPROVAL, Nurse_List_BY_branch_ID
} from '../constents/constents';
import jwt_decode from "jwt-decode";
import Cookies from "universal-cookie";

const cookies = new Cookies();
const userToken = cookies.get("token") ? cookies.get("token") : null
const userData = cookies.get("userData") ? cookies.get("userData") : null
const initState = {
    loading: false,
    regeisterErrors: [],
    loginErrors: [],
    token: userToken,
    user: userData
};
console.log("this is user data" , initState.user)
const verifyToken = token => {
    const decodedToken = jwt_decode(token);
    const expiresIn = new Date(decodedToken.exp * 1000);
    if (new Date() > expiresIn) {
        localStorage.removeItem('myToken');
    }
    else {
        return decodedToken;
    }

}
const token = localStorage.getItem('myToken');

if (token) {
    const decoded = verifyToken(token);
    initState.token = token;
    const { user } = decoded;
    initState.user = user;
}

const userReducer = (state = initState, action) => {
    switch (action.type) {

        case Nurse_List_BY_branch_ID:
            return { ...state, nurseListsByAgencyId: action.payload }

        case USER_SIGNIN_SUCCESS:
            // const decoded = verifyToken(action.payload);
            // const { user } = decoded;
            // console.log("user dispatch" , user)
            return { ...state,  user: action.payload , token: action.payload }
        case USER_SIGNIN_FAIL:
            return { loading: false, loginErrors:  action.payload };
        case USER_SIGNOUT:
            return { ...state, token: '', user: '' }


        case USER_CHANGE_PASSWORD:
            return { loading: false, passwordUpdate: action.payload, loginErrors: '' };

        case DASHBAORD_DATA:
            return { ...state, getDashbaord: action.payload }

        case USER_APPROVAL:
            return { ...state, adminApprovalStatus: action.payload }

        case USER_LIST:
            return { ...state, loading: true, users: action.payload, adminApprovalStatus: '' };

        default:
            return state;

    }



}

export default userReducer;