import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import "react-datepicker/dist/react-datepicker.css";
import { userDetails } from "../../actions/agencyAction";
import AsyncCsv from "./UserDetailsCsv";

const UserDetails = (props) => {
  const dispatch = useDispatch();
  const { errors, userDetail } = useSelector((state) => state.agencyReducer);
  console.log(userDetail?.preferences?.spotify_list);
  console.log("user edit rudcer", userDetail);
  const { edit } = useParams();

  const [state, setState] = useState({
    id: "",
    username: "",
    email: "",
    phone_number: "",
    user_description: "",
    user_gender: "",
    user_image: "",
    cover_image: "",
    interest: "",
    preferences: [],
  });

  useEffect(() => {
    // dispatch(allAgency());
    dispatch(userDetails(edit));
  }, []);

  // for errors
  useEffect(() => {
    if (errors && errors.length > 0) {
      errors.map((error) => toast.error(error.message));
    }
  }, [errors]);

  useEffect(() => {
    if (edit) {
      if (userDetail) {
        setState({
          ...state,
          id: userDetail._id,
          username: userDetail.username,
          email: userDetail.email,
          phone_number: userDetail.phone_number,
          user_description: userDetail.user_description,
          user_gender: userDetail.user_gender,
          user_image: userDetail.user_image,
          cover_image: userDetail.cover_image,
          preferences: userDetail.preferences,
          //   interest: userDetail?.preferences.interest,
        });
      }
    }
  }, [userDetail]);
  console.log("abcccccc=========", state);
  return (
    <>
      <div className="container-fluid">
        <div className="block-header">
          <div className="row clearfix">
            <div className="col-md-6 col-sm-12">
              <h2>User Details</h2>
              <div>
                <AsyncCsv id={state.id} />
              </div>
            </div>
          </div>
        </div>
        <div className="row clearfix">
          <Toaster
            position="top-right"
            reverseOrder={false}
            toastOptions={{
              style: {
                border: "1px solid #713200",
                padding: "16px",
                color: "#713200",
                fontSize: "17px",
              },
            }}
          />

          <div className="col-xl-12 col-lg-8 col-md-7">
            <div className="card">
              <div className="body">
                <div className="row clearfix">
                  <div className="col-lg-3 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Name</label>
                      <p>{state.username}</p>
                    </div>
                  </div>
                  <div className="col-lg-3 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Email</label>
                      <p>{state.email}</p>
                    </div>
                  </div>
                  <div className="col-lg-3 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Gender</label>
                      <p>{state.user_gender}</p>
                    </div>
                  </div>
                  <div className="col-lg-3 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Phone Number</label>
                      <p>{state.phone_number}</p>
                    </div>
                  </div>
                  <div className="col-lg-12 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Description</label>
                      <p>{state.user_description}</p>
                    </div>
                  </div>

                  <div className="col-lg-12 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Interest</label>
                      <p>{userDetail?.preferences?.interest}</p>
                    </div>
                  </div>

                  <div className="col-lg-12 col-md-12">
                    <div className="form-group">
                      <label style={{ color: "#17C2D7" }}>Spotify List</label>
                    </div>
                    <div>
                      <ul>
                        {userDetail?.preferences?.spotify_list?.map(
                          (data, index) => (
                            <div key={index}>
                              <li>{data}</li>
                            </div>
                          )
                        )}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default UserDetails;
