
import UserList from "./Admin/UserList";
import UserDetails from "./Admin/UserDetails";



import Dashboard from "./Dashboard/dashboard";

import PP from "./Admin/PP";
import TC from "./Admin/TC";
import HS from "./Admin/HS";
import Notifications from "./Admin/Notification";




const Routes = [

 
  {
     path: "/user-list",
    name: "User List",
    exact: true,
    pageTitle: "User List",
    // isPrivate: true,
    component: UserList,
  },
  
  {
    path: "/user-details/:edit?",
    name: "User Details",
    exact: true,
    pageTitle: "User Details",
    // isPrivate: true,
    component: UserDetails,
  },

  
  {
    path: "/contents/pp",
    name: "Privacy Policy",
    exact: true,
    pageTitle: "Privacy Policy",
    // isPrivate: true,
    component: PP,
  },
  {
    path: "/contents/tc",
    name: "Terms & Condition",
    exact: true,
    pageTitle: "Terms & Condition",
    // isPrivate: true,
    component: TC,
  },
  {
    path: "/contents/hs",
    name: "Help & Support",
    exact: true,
    pageTitle: "Help & Support",
    // isPrivate: true,
    component: HS,
  },
  {
    path: "/notifications",
    name: "Notifications",
    exact: true,
    pageTitle: "Notifications",
    // isPrivate: true,
    component: Notifications,
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    exact: true,
    pageTitle: "Dashboard",
    component: Dashboard,
  },
  
];

export default Routes;
