import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import { privacyPo, updatedPP } from "../../actions/agencyAction";
import TextField from "@material-ui/core/TextField";

const PP = (props) => {
  const dispatch = useDispatch();
  // let ppm = new URLSearchParams(props.location.search).get("content_type");

  const { privacyPol, ppUpdate } = useSelector((state) => state.agencyReducer);
  console.log("pri", privacyPol);
  const [state, setState] = useState({
    pp: "pp",
    content: "Please Write ",
  });

  const saveNote = async (e) => {
    e.preventDefault();
    console.log("here type", state);
    //state.id = edit ? edit: '';
    dispatch(updatedPP(state));
  };

  useEffect(() => {
    //    dispatch(privacyPo(ppm));
    dispatch(privacyPo());
    //updateContent();
  }, [ppUpdate]);

  useEffect(() => {
    if (privacyPol != undefined) {
      setState({
        ...state,
        content: privacyPol.content,
      });
    }
  }, [privacyPol]);

  const updateContent = (newContent) => {
    setState({
      content: newContent,
    });
  };

  const onChange = (e) => {
    // var newContent = evt.editor.getData();
    var pp = "pp";
    setState({
      content: e.target.value,
      pp: pp,
    });
  };
  // Update pp
  useEffect(() => {
    if (ppUpdate) {
      toast.success(ppUpdate.message);
      setState({
        content: "",
      });
    }
  }, [ppUpdate]);

  return (
    <>
      <div className="container-fluid">
        <div className="block-header">
          <div className="row clearfix">
            <div className="col-md-6 col-sm-12">
              <h2>Privacy Policy</h2>
            </div>
          </div>
        </div>
        <div className="row clearfix">
          <Toaster
            position="top-centre"
            reverseOrder={false}
            toastOptions={{
              style: {
                border: "1px solid #713200",
                padding: "16px",
                color: "#713200",
                fontSize: "17px",
              },
            }}
          />

          <div className="col-xl-8 col-lg-8 col-md-7">
            <form onSubmit={saveNote}>
              <div className="card">
                <div className="body">
                  <div className="row clearfix">
                    <div className="col-lg-12 col-md-12">
                      <div className="form-group">
                        {/* <input type="hidden" name="content_type" value={state.content_type} /> */}
                        {/* <label style={{ color: '#17C2D7' }}>Privacy Policy</label> */}
                        {/* <CKEditor
                                                content={state.content_content}
                                                events={{
                                                    "change": onChange
                                                }}
                                            /> */}
                        <TextField
                          onChange={(e) => onChange(e)}
                          value={state.content}
                          variant="outlined"
                          margin="normal"
                          required
                          fullWidth
                          id="content"
                          label="Privacy Policy"
                          multiline
                          rows={10}
                          maxWidth
                          style={{ width: 395 }}
                          name="content"
                        />
                      </div>
                    </div>
                  </div>

                  <button
                    type="submit"
                    className="btn btn-round btn-primary mr-1"
                    style={{ backgroundColor: "rgb(23 8 60 / 93%)" }}
                  >
                    Save
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
export default PP;
