import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import { notifications } from "../../actions/agencyAction";
import TextField from "@material-ui/core/TextField";

const Notification = (props) => {
  const dispatch = useDispatch();

  const { notification } = useSelector((state) => state.agencyReducer);
  const [state, setState] = useState({
    title: "",
    message: "",
  });

  const saveNote = async (e) => {
    e.preventDefault();
    console.log(state);
    dispatch(notifications(state));
  };

  const onChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });

    console.log(state);
  };

  //   // Update pp
  useEffect(() => {
    if (notification) {
      toast.success(notification.message);
      setState({
        title: "",
        message: "",
      });
    }
  }, []);

  return (
    <>
      <div className="container-fluid">
        <div className="block-header">
          <div className="row clearfix">
            <div className="col-md-6 col-sm-12">
              <h2>Notifications</h2>
            </div>
          </div>
        </div>
        <div className="row clearfix">
          <Toaster
            position="top-centre"
            reverseOrder={false}
            toastOptions={{
              style: {
                border: "1px solid #713200",
                padding: "16px",
                color: "#713200",
                fontSize: "17px",
              },
            }}
          />

          <div className="col-xl-8 col-lg-8 col-md-7">
            <form onSubmit={saveNote}>
              <div className="card">
                <div className="body">
                  <div className="row clearfix">
                    <div className="col-lg-12 col-md-12">
                      <div className="form-group">
                        <TextField
                          onChange={(e) => onChange(e)}
                          value={state.title}
                          variant="outlined"
                          margin="normal"
                          required
                          fullWidth
                          id="title"
                          label="Title"
                          name="title"
                        />
                        <TextField
                          onChange={(e) => onChange(e)}
                          value={state.message}
                          variant="outlined"
                          margin="normal"
                          required
                          fullWidth
                          id="message"
                          label="Message"
                          multiline
                          rows={8}
                          name="message"
                        />
                      </div>
                    </div>
                  </div>

                  <button
                    type="submit"
                    className="btn btn-round btn-primary mr-1"
                    style={{ backgroundColor: "rgb(23 8 60 / 93%)" }}
                  >
                    Send
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};
export default Notification;
