import axios from "axios";
import instance from "../components/Config/axios";
import Cookies from "universal-cookie";
import {
  USER_SIGNIN_SUCCESS,
  USER_SIGNIN_FAIL,
  USER_SIGNOUT,
  USER_LIST,
  USER_LIST_FAIL,
  USER_EDIT,
  USER_CHANGE_PASSWORD,
  DASHBAORD_DATA,
  USER_APPROVAL,
} from "../constents/constents";

// axios.defaults.baseURL = "http://localhost:3010"
const cookies = new Cookies();

export const signin = (state) => {
  return async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    try {
      // const  {data}  = await axios.post("/api/login", state, config);
       const { data } = await axios.post(process.env.REACT_APP_API_URL + '/api/admin/login', state, config);

      // localStorage.setItem('myToken', data.token)
      // console.log(data)
      cookies.set("token", data.token);
      cookies.set("userData", data.data)
       const user = data.data;
       const token = data.token
       console.log("sds",token)
      dispatch({ type: USER_SIGNIN_SUCCESS, payload: user, token });
    } catch (error) {
      // console.log(error.response.data.message)
      dispatch({ type: USER_SIGNIN_FAIL, payload:  error.response.data });
    }
  };
};

// For Get for Edit user
export const getUserById = (userId) => {
  return async (dispatch) => {
    try {
      const {
        data: { getUser, getFamilyInfo, getEmploymentInfo, getEducationInfo },
      } = await instance.get(`/api/userEdit/${userId}`);
      // const { data: { getUser, getFamilyInfo, getEmploymentInfo, getEducationInfo } } = await instance.get(process.env.REACT_APP_API_URL + `/api/userEdit/${userId}`);

      dispatch({
        type: USER_EDIT,
        payload: {
          getUser,
          getFamilyInfo,
          getEmploymentInfo,
          getEducationInfo,
        },
      });
    } catch (error) {
      dispatch({ type: USER_LIST_FAIL, payload: error.response.data.error });
    }
  };
};

// Here update the user Password
export const passwordSave = (state) => {
  return async (dispatch) => {
    // const config = {
    //   headers: {
    //     "Content-Type": "application/json",
    //   },
    // };
    try {
      // const { data } = await axios.post("/api/passwordChange", state, config);
       const { data } = await axios.post(process.env.REACT_APP_API_URL + '/api/admin/changePassword', state);
        console.log(data)
      dispatch({ type: USER_CHANGE_PASSWORD, payload: data });
    } catch (error) {
      dispatch({ type: USER_SIGNIN_FAIL, payload: error.response.data });
    }
  };
};

export const signout = () => (dispatch) => {
  //localStorage.removeItem('myToken');
  cookies.remove("token");
  cookies.remove("userData");
  dispatch({ type: USER_SIGNOUT });
};

// here to get Alla user
export const userlist = () => async (dispatch) => {
  try {
    const userData = cookies.get("userData");
    const user_id = userData._id;
    // console.log("user_id_in_nurse_list:", user_id);
    const { data } = await axios.get(`/api/userList/${user_id}`);
    // const { data } = await axios.get(process.env.REACT_APP_API_URL + `/api/userList/${user_id}`);

    dispatch({ type: USER_LIST, payload: data });
  } catch (error) {
    dispatch({ type: USER_SIGNIN_FAIL, payload: error.response.data.error });
  }
};

