import React,{useEffect, useState} from 'react';
import {useDispatch,useSelector} from 'react-redux';
import { dashbaordData } from '../../actions/agencyAction';
import axios from 'axios';
import { AiFillAndroid,BsApple,FaUsers,FaUsersSlash } from 'react-icons/all';


const Dashboard = () => {

	
	const dispatch = useDispatch();
	const {getDashbaord} = useSelector((state) => state.userReducer);


console.log("dashboard" , getDashbaord)
	 
	 
	 useEffect(() => {
            dispatch(dashbaordData());
		//    getagencydashboard()
	 },[dashbaordData])


	return (
		<div className="container-fluid">
			<div className="block-header">
				<div className="row clearfix">
					<div className="col-md-6 col-sm-12">
						<h1>Welcome to Ravebae </h1>						 
					</div>					
				</div>
			</div>

			<div className="row clearfix">
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg text-white rounded-circle" style = {{backgroundColor : 'rgb(57 41 98 / 93%)'}}> <FaUsers style={{fontSize:"25px"}} /> </div>
								<div className="ml-4">
									<span>Total Users</span>
									<h4 className="mb-0 font-weight-medium">{ getDashbaord? getDashbaord.users : 0 } </h4>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg text-white rounded-circle" style = {{backgroundColor : 'rgb(57 41 98 / 93%)'}}><FaUsersSlash style={{fontSize:"25px"}} /> </div>
								<div className="ml-4">
									<span>Blocked Users</span>
									<h4 className="mb-0 font-weight-medium">{ getDashbaord? getDashbaord.blocked : 0 } </h4>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg text-white rounded-circle" style = {{backgroundColor : 'rgb(57 41 98 / 93%)'}}><AiFillAndroid style={{fontSize:"25px"}}/></div>
								<div className="ml-4">
									<span>Android Users</span>
									<h4 className="mb-0 font-weight-medium">{ getDashbaord? getDashbaord.android : 0 } </h4>
									
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className="col-lg-3 col-md-6">
					<div className="card">
						<div className="body">
							<div className="d-flex align-items-center">
								<div className="icon-in-bg text-white rounded-circle" style = {{backgroundColor : 'rgb(57 41 98 / 93%)'}}><BsApple style={{fontSize:"20px"}}/></div>
								<div className="ml-4">
									<span>IOS Users</span>
									<h4 className="mb-0 font-weight-medium">	{ getDashbaord? getDashbaord.ios : 0 }  </h4>
								
								</div>
							</div>
						</div>
					</div>
				</div>
				
			</div>

			
		</div>
	);

}
export default Dashboard
