import React, { useEffect, useState } from "react";
import { MDBDataTableV5} from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { Link , useParams } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";
import {CircularProgress} from "@material-ui/core"
// import {Spinner} from "../UI/Loader";
import AsyncCsv from "./UserCsv";
import { allUsers, blockUser, unblockUser } from "../../actions/agencyAction";

const UserList = (props) => {
  const dispatch = useDispatch();
  const { userList, blockuser, unBlockUser } = useSelector(
    (state) => state.agencyReducer
  );
  // const { user } = useSelector((state) => state.userReducer);
  const [tablebody, setTableBody] = useState([]);
  console.log(userList, "reduceer");

  useEffect(() => {
    dispatch(allUsers());
  }, []);

  const BlockUser = (user_id, e) => {
    e.preventDefault();
    dispatch(blockUser(user_id));
    dispatch(allUsers());
  };

  const UnblockUser = (user_id, e) => {
    e.preventDefault();
    dispatch(unblockUser(user_id));

    // console.log("===============>")
    dispatch(allUsers());
  };

  useEffect(() => {
    if (blockuser) {
      toast.success(blockuser.message);
    }
    dispatch(allUsers());
  }, [blockuser]);

  // unBlock User
  useEffect(() => {
    if (unBlockUser) {
      toast.success(unBlockUser.message);
    }
    dispatch(allUsers());
  }, [unBlockUser]);

  useEffect(() => {
    if(!userList){
      return <>
      </>
    }
    else if (userList) {
      const userDataFiltered = userList?.map((x) => {
        //Getting user image
        x.user_image = x.user_image ? (
          <img
            src={process.env.REACT_APP_API_URL + `/${x.user_image}`}
            alt="image"
            className="someImgClass"
            width="50px"
            height="50px"
          />
        ) : (
          <img
            src={"../assets/images/avatar.png"}
            alt="image"
            className="someImgClass"
            width="50px"
            height="50px"
          />
        );
        let jsx = (
          <>
            <Link to={`user-details/${x._id}`}>
              <button
                type="button"
                className="btn btn-sm btn-default"
                title="View Details"
                data-for="send"
                data-tip="true"
                currentitem="false"
              >
                <i className="icon-eye"></i>
              </button>
            </Link>

            {/* button condition */}
            {x?.is_blocked === 0 ? (
              <button
                //  to={'*'}
                onClick={(e) => BlockUser(x._id, e)}
                className="btn btn-sm btn-primary mr-1 ml-1"
                title=""
                style={{ backgroundColor: "rgb(23 8 60 / 93%)" }}
              >
                Deactivate
              </button>
            ) : (
              <button
                //  to={'*'}
                onClick={(e) => UnblockUser(x._id, e)}
                className="btn btn-sm btn-primary mr-1 pr-4 ml-1 justify-content-center "
                style={{ backgroundColor: "rgb(23 8 60 / 93%)" }}
                title=""
              >
                Activate
              </button>
            )}
          </>
        );

        x.action = jsx;
        return x;
      });

      setTableBody({
        columns: [
          // {
          // 	label: 'Image',
          // 	// field: 'user_image',
          //   options: {
          //     customBodyRender: () => {
          //       return (
          //         <div>
          //           <img src="../../assets/images/logo-technado--.png" class="img-fluid img-thumbnail" alt="image" />
          //           </div>
          //       )
          //     }
          //   },
          // 	width: 50,
          // },
          {
            label: "Image",
            field: "user_image",
            sort: "asc",
            width: 50,
          },
          {
            label: "Name",
            field: "username",
            width: 50,
          },
          {
            label: "Email",
            field: "email",
            width: 50,
          },
          {
            label: "Phone",
            field: "phone_number",
            width: 50,
          },
          {
            label: "Gender",
            field: "user_gender",
            width: 50,
          },
          {
            label: "Device Type",
            field: "user_device_type",
            width: 50,
          },
          {
            label: "Action",
            field: "action",
            width: 50,
          },
        ],

        rows: userDataFiltered ? userDataFiltered : [],
      });
    }
  }, [userList]);

  return (
    <>
      <div className="row clearfix">
        <div className="col-lg-12">
          <div className="card">
            <div className="header" style={{ marginTop: 20 }}>
              <h4>Users List </h4>
              {/* <Link
                to="/branch-add"
                className="btn btn-sm btn-primary mr-1"
                title=""
              >
                Add New Branch
              </Link> */}
              <div>
                <AsyncCsv />
              </div>

              <Toaster
                position="top-center"
                reverseOrder={false}
                toastOptions={{
                  style: {
                    border: "1px solid #713200",
                    padding: "16px",
                    color: "#713200",
                    fontSize: "17px",
                  },
                }}
              />
            </div>
            <div className="body">
              <div className="table-responsive">
                  
                {userList?.length <1?(
                     
                       <CircularProgress className="progress"  style={{ backgroundColor: "transparent", color: "indigo" ,display: "flex", alignItems: "center", justifyContent: "center", display: "flex",}}/>
                
                ): (
                  <MDBDataTableV5
                    noBottomColumns
                    className="table table-hover js-basic-example dataTable table-custom spacing5"
                    entriesOptions={[5, 10, 20, 25]}
                    entries={5}
                    paging
                    pagesAmount={15}
                    data={tablebody}

                    // searchTop
                    // sortRows={['user_name']}
                    //searchBottom={true}
                    // onPageChange={()=>{ activePage: 2, pagesAmount: 5 }}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default UserList;
