// here to get All Users
export const allUsers = () => async (dispatch) => {
  try {
    // const { data: { branchesList } } = await axios.get('/api/getAllBranches');
    // const { data } = await axios.get(`/api/dashboard/users`);
    const { data } = await axios.get(process.env.REACT_APP_API_URL + '/api/dashboard/users');
    // console.log("action", );

    const userList = data.data.user;
    dispatch({ type: ALL_USERS, payload: userList });
  } catch (error) {
    dispatch({ type: ERROR_AGENCY, payload: error.response.data.error });
  }
};
// Here edit Branch
export const userDetails = (user_id) => {
  return async (dispatch) => {
    try {
      // const { branchEdit }
      //  = await axios.get(`/api/dashboard/users/${user_id}`);
      const { data } = await axios.get(
        process.env.REACT_APP_API_URL + `/api/dashboard/user/${user_id}`
      );
      console.log("userID data", data);
      dispatch({ type: USER_DETAILS, payload: data?.data });
    } catch (error) {
      dispatch({ type: ERROR_AGENCY, payload: error.response.data.message });
    }
  };
};




