import React, { useState,useEffect } from 'react';
import { Link } from 'react-router-dom';
import {useDispatch,useSelector} from 'react-redux';
import toast, { Toaster } from 'react-hot-toast';
import { passwordSave } from '../../actions/userAction';

const  ForgotPassword = () => {
          
	 const dispatch = useDispatch();
	 const{passwordUpdate,loginErrors} = useSelector((state) => state.userReducer);
 console.log(loginErrors , "reducer")
		const [state, setState] = useState({
			email : ''
		});
          
		const handleInput = (e) => {                  
				  setState({
					...state,
					  [e.target.name] : e.target.value
				  })
		  }

      const postData = (e) => {
		
		e.preventDefault();  
		  dispatch(passwordSave(state));
	  }

	  useEffect(() => {
		if(passwordUpdate){
			console.log(passwordUpdate)
			toast.success(passwordUpdate.message);
			setState({
				email:''                    
			})
		}            
	  },[])

			// for errors
			useEffect(() => {                       
				if (loginErrors) {
					console.log(loginErrors.message)
					toast.error(loginErrors.message);
				  }
			},[loginErrors])

		return (
			<>
				{/* <div class="pattern">
					<span class="red"></span>
					<span class="indigo"></span>
					<span class="blue"></span>
					<span class="green"></span>
					<span class="orange"></span>
				</div> */}
				<div className="auth-main particles_js" style = {{backgroundColor : 'rgb(23 8 60 / 93%)'}}>
					<div className="auth_div vivify popIn">
						<div className="auth_brand">
						<Toaster
						position="top-center"
						reverseOrder={false}
						toastOptions={{							
							style: {
							  border: '1px solid #713200',
							  padding: '16px',
							  color: '#713200',
							  fontSize: '17px'
							},
						  }}
						/>
						<img
              src="../assets/images/ravebaelogo.png"
              style={{ width: 120, height: 100 }}
              className="d-inline-block align-top mr-2"
              alt="Brappy"
            />
						</div>
						<div className="card forgot-pass">
							<div className="body">
								<p className="lead mb-3"> forgot something?</p>
								<p>Type email to recover password.</p>
								<form className="form-auth-small" onSubmit={postData}>
									<div className="form-group">
										<input type="text" name="email" className="form-control round" onChange={handleInput} value={state.email}  placeholder="Email"  style = {{color : 'rgb(23 8 60 / 93%)'}} />
									</div>
									<button type="submit" className="btn btn-round btn-primary btn-lg btn-block"  style = {{backgroundColor : 'rgb(23 8 60 / 93%)'}}>RESET PASSWORD</button>
									<div className="bottom">
										<span className="helper-text">Know your password? <Link to="/login"  style = {{color : 'rgb(23 8 60 / 93%)'}}>Login</Link></span>
									</div>
								</form>
							</div>
						</div>
					</div>
					<div id="particles-js"></div>
				</div>
			</>
			);
		}

export default ForgotPassword
